body {
  text-align: center;
  margin: 0;
}

/******************************CLASS TAGS*******************************/
h1 {
  font-family: "Londrina Outline", cursive;
  font-size: 4em;
  margin-top: 0;
  margin-bottom: 20px;
}

.hooked {
  width: 80px;
  height: 80px;
  margin-top: 5px;
}

#main-container {
  z-index: 1;
}

canvas {
  border: 2px solid rgba(0, 0, 0, 0.479);
  background: radial-gradient(
    circle,
    rgb(19, 92, 92) 0%,
    rgb(159, 214, 231) 100%
  );
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
}

.container {
  margin-top: 0;
  width: 100%;
  height: 10%;
}

.dashes {
  margin-top: 5%;
}

.letter {
  display: inline-block;
  color: black;
  font-size: 40px;
  margin: 10px;
  background: radial-gradient(
    circle,
    rgb(155, 243, 243) 0%,
    rgb(8, 71, 90) 100%
  );
  border: 2px solid black;
  padding: 0 20px;
  border-radius: 20%;
  font-family: "Dokdo", cursive;
}

.buttonDiv {
  position: relative;
  height: 50px;
}
.myInput {
  position: absolute;
  left: 60%;
  height: 50%;
  border-radius: 15%;
}
.submitButton {
  position: absolute;
  left: 70%;
  height: 55%;
  border-radius: 15%;
}
.myButton {
  text-decoration: black;
  border: 2px solid black;
  color: black;
  padding: 5px;
  position: absolute;
  left: 48%;
  border-radius: 30%;
  font-family: "Cute Font", cursive;
  font-size: 30px;
  background-color: rgb(180, 210, 217);
}
.myButton:hover {
  background-color: rgb(80, 180, 191);
}

#category-select {
  position: absolute;
  left: 20%;
  top: 65%;
  height: 20px;
}

.pickCat {
  position: absolute;
  left: 20%;
  top: 0;
  font-size: 30px;
  font-family: "Cute Font", cursive;
}

.hangman-img {
  position: relative;
}

.hanger {
  height: 300px;
  width: 300px;
  position: relative;
  right: 12%;
}

.kaguya1,
.kaguya {
  position: absolute;
  width: 300px;
  height: 300px;
  left: 35%;
  top: 25%;
}

@-webkit-keyframes animate5 {
  0% {
    left: 0px;
    right: 0px;
  }
  100% {
    left: 100px;
    right: 0;
  }
}

@-webkit-keyframes animate6 {
  0% {
    left: 900px;
    right: 0px;
  }
  100% {
    left: 450px;
    right: 0px;
  }
}

.kaguya5 {
  position: absolute;
  width: 700px;
  height: 300px;
  left: 5%;
  top: 15%;
  -webkit-animation-name: animate5;
  -webkit-animation-duration: 4s;
}

.kaguya6 {
  position: absolute;
  width: 700px;
  height: 300px;
  right: 20%;
  top: 15%;
  -webkit-animation-name: animate6;
  -webkit-animation-duration: 4s;
}

.life-span {
  border: 2px solid black;
  padding: 10px 30px 10px 10px;
  position: absolute;
  left: 88%;
}

.score-span {
  border: 2px solid black;
  padding: 10px 30px 10px 10px;
  position: absolute;
  left: 5%;
}

.poof {
  visibility: hidden;
}

.correct-letter {
  color: black;
  font-size: 40px;
  font-family: "Dokdo", cursive;
}

.myDivs {
  display: inline-block;
  margin: 10px;
  border-bottom: 5px solid rgb(11, 16, 19);
  padding: 0 10px;
}

@media only screen and (max-width: 600px) {
  .getHooked {
    font-size: 40px;
  }
  .hooked {
    width: 40px;
    height: 40px;
  }
  .score-span {
    padding: 1px;
  }
  .pickCat {
    font-size: 30px;
    position: absolute;
    top: -33px;
  }
  #category-select {
    margin-left: 20px;
    width: 90px;
  }
  .myButton {
    position: absolute;
    top: 15px;
    font-size: 15px;
    margin-left: 5px;
  }
  .life-span {
    padding: 2px;
  }
  .letter {
    font-size: 20px;
    padding: 0 10px;
  }
  .hanger {
    width: 150px;
    height: 150px;
  }
  .myDivs {
    width: 15px;
    height: 30px;
  }
  .correct-letter {
    font-size: 30px;
  }
  .kaguya {
    width: 150px;
    height: 150px;
  }
  @-webkit-keyframes animate5 {
    0% {
      left: 0px;
      right: 0px;
    }
    100% {
      left: 30px;
      right: 0;
    }
  }
  .kaguya5 {
    position: absolute;
    width: 350px;
    height: 150px;
    left: 2%;
    top: 15%;
    -webkit-animation-name: animate5;
    -webkit-animation-duration: 4s;
  }
  @-webkit-keyframes animate6 {
    0% {
      left: 450px;
      right: 0px;
    }
    100% {
      left: 100px;
      right: 0px;
    }
  }
  .kaguya6 {
    position: absolute;
    width: 350px;
    height: 150px;
    right: 2%;
    top: 15%;
    -webkit-animation-name: animate6;
    -webkit-animation-duration: 2s;
  }
}
